import React, { Component } from 'react'; 

import MainButton from '../buttons/Buttons';
import ListElement from '../lists/List';
import bioSelected from '../../actions';    


class Main extends Component {
    constructor (props) {
        super(props);
        this.state = {}; 
    }
    render () {
        return (
            <main className="center flex-column">
                <div className="flex-column flex-center titleContainer">
                    <div className="logo"></div>
                    <h1 className="title">Bernardo Soriano</h1>
                </div>
                <div className="flex-row flex-center subTitleContainer">
                    <h2>Software Engineer at BCG Platinion North America and Maya Design</h2>
                </div>
                <div className="flex-row flex-top buttonsContainer">
                    {/* <MainButton btnName="Bio"></MainButton>
                    <MainButton btnName="Experience"></MainButton>
                    <MainButton btnName="Showcase"></MainButton>
                    <MainButton btnName="Connect"></MainButton> */}
                    <ListElement route="bio"></ListElement>
                    <ListElement route="contact"></ListElement>
                </div>
            </main>
        )        
    }
}

export default Main; 


