//Importing all actions
import { SELECT_BIO, SELECT_EXPERIENCE, SELECT_SHOWCASE, SELECT_CONNECT, SHOW_MENU, HIDE_MENU, bioSelected } from '../actions/index';

const homeReducer = (state = {}, action ) => {
    switch (action.type) {
        case 'SELECT_BIO':
            debugger;
            console.log("Selected: SELECT_BIO");
            return "SELECT_BIO"
        default:
            console.log("::Default state::");
            return state
    }
}

export default homeReducer;


