// React core
import React, { Component } from 'react';
// Styles
import './HomeContainer.css';
import '../text/Titles.css';
// Components
import MainButton from '../buttons/Buttons';

import Menu from '../menu/Menu'

import Main from '../main/Main';
import Contact from '../contact/Contact';
import Bio from '../bio/Bio';

//Home Actions
import { SELECT_BIO, SELECT_EXPERIENCE, SELECT_SHOWCASE, SELECT_CONNECT, SHOW_MENU, HIDE_MENU } from '../../actions/index';

//Home reducers
import homeReducer from '../../reducers/index';
//Redux

import { Provider } from "react-redux";
import { createStore, applyMiddleware } from "redux";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import promise from "redux-promise";

const createStoreWithMiddleware = applyMiddleware(promise)(createStore);

class Home extends Component {
    constructor (props) {
        super(props);
        this.state = {};
    }
    render () {
        return (
            <div className="HomeContainer">
                {/* Background Cover */}
                <div className="backgroundCover">
                </div>
                {/* Overlay */}
                <div className="overlay">
                </div>
                {/* Menu Overlay */}
                <Menu></Menu>                
                {/* Elements container */}
                <div className="elementsContainer flex-column">
                    {/* Header */}
                    <header className="top flex-row">

                    </header>

                    <Provider store={createStoreWithMiddleware(homeReducer)}>
                        <BrowserRouter>
                            <Switch>
                                <Route path="/contact" component={Contact}></Route>
                                <Route path="/bio" component={Bio}></Route>
                                <Route path="/" component={Main}></Route>
                            </Switch>
                        </BrowserRouter>
                    </Provider>
                    
                    
                    {/* Footer */}
                    <footer className="bottom flex-row">
                        <footer>Carefully crafted with React and SASS | Design, Code and Photography by @bersoriano | Brooklyn, NY</footer>
                    </footer>
                </div>                
            </div>            
        )
    }
}
export default Home;


