import React, { Component } from 'react';
import './Bio.css'

import Link from 'react-router-dom/Link';

class Bio extends Component {
    constructor (props) {
        super(props);
        this.state = {};
    }
    render () {
        return (
            <main className="bio center flex-column">
                <div className="flex-row flex-center presenter">
                </div>                
                <div className="flex-column flex-center presenterContent">
                    <header className="flex-row flex-center">
                            <Link className="backBtn" to={`main`}>Back</Link>
                    </header>
                    <section className="flex-column flex-center">
                        <div className="profilePic"></div>
                        <h1 className="title">Bernardo Soriano</h1>
                        <div className="divider"></div>
                        <p className="regularParagraph">
                            Bernardo has been developing applications for capital markets, insurance, investment and consumer banking companies for the last 6 years.
                        </p>
                        <p className="regularParagraph">His passion is driven by the experience delivered to the users, being the people the most important thing around designing and engineering products.</p>
                        <p className="regularParagraph">In his free time he enjoys designing, coding and traveling with his friends.</p>
                    </section>
                    <footer className="flex-row flex-center">

                    </footer>                    
                </div>
            </main>
        )
    }
} 

export default Bio;


