import React, { Component } from 'react';
import MainButton from '../buttons/Buttons';
import './Contact.css';


import Link from 'react-router-dom/Link';

import '../buttons/Buttons.css';

//Components
import Cards from '../cards/Cards';

//Models
import contactCardCollection from '../../models/contactSection';

class Contact extends Component {
    constructor (props) {
        super(props);
        this.state = {};
    }
    render () {
        return (
            <main className="center flex-column contactSection">
                {/* Header */}
                <header className="flex-row flex-center">
                    <Link className="backBtn" to={`main`}>Back</Link>
                    <span className="sectionTitle">Lets get in touch</span>
                </header>
                {/* Content - Buttons Row */}
                <div className="flex-row flex-center buttonsRow">
                    <MainButton btnName="LinkedIn" btnUrl="https://www.linkedin.com/in/bernardosoriano/"></MainButton>
                    <MainButton btnName="Twitter" btnUrl="https://twitter.com/bersoriano"></MainButton>
                    <MainButton btnName="Github" btnUrl="https://github.com/bersoriano/"></MainButton>
                    <MainButton btnName="Email" btnUrl="mailto:bersoriano@me.com"></MainButton>
                </div>
                {/* Content - Cards Row */}
                <div className="flex-row flex-center cardsRow">
                    { 
                        contactCardCollection.map(function(card){
                            console.log("detalles: "+card.details);
                            return <Cards cardTitle={card.title} cardDescription={card.desc} skills={card.details}></Cards>
                        })
                    }
                </div>
            </main>
        )
    }
}

export default Contact;


